@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@font-face {
	font-family: 'SegoeUI';
	src: local('SegoeUI'), url(/static/media/segoe-ui.c4b43d1f.otf) format('otf');
}
@font-face {
	font-family: 'Helvetica';
	src: local('Helvetica'), url(/static/media/helvetica.a778f716.ttf) format('ttf');
}

body {
	width: 100% !important;
	max-width: 100% !important;
}

#root {
	width: 100% !important;
	overflow-x: hidden !important;
}
h4 {
	font-family: 'SegoeUI' !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 0.9rem !important
}

.contato-descricao span {
	color: #fff !important;
	font-weight: normal;
	font-family: 'SegoeUI' !important;
	font-size: 0.8rem !important

}

/* MENU */
.rs-nav-horizontal {
	display: flex !important;
	justify-content: center !important;
	height: 40px !important;
}

.rs-nav-horizontal ul {
	width: 80% !important;
    display: flex !important;
	justify-content: space-between !important;
	align-items: flex-end !important;
}

.rs-nav-horizontal ul li a::before{
	background-color: #00419F !important;
}
.rs-nav-horizontal ul li a {
	color: #707070 !important;
	font-weight: 600 !important;
	font-family: 'SegoeUI' !important;
	font-size: 1rem !important
}

/* Carrossel */

.atendimento-depoimentos{
	background-color: #FFF8F9;
	padding:50px;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: center;
	height:100%;
  }

.homeCarrossel {
	height: 500px !important;
	margin-top: 120px !important
}
.empresaCarrossel {
	height: 450px !important;
	margin-top: 0px !important;
	/* padding-bottom: 80px !important */

}
.empresaCarrossel .awssld__content {
	background: #f7f7f7 !important;

}

.empresaCarrossel .awssld__bullets {
	bottom: 70px !important;
	z-index: 9999 !important;
}

.empresaCarrossel .awssld__bullets button:not(.awssld__bullets--active) {
	background: #FFF !important;
	border: 1px solid rgb(0,73,183) !important
}

.empresaCarrossel .awssld__bullets--active {
	background: rgb(0,73,183) !important;
	background: linear-gradient(20deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
}
.slider_1 {
	background: url(/static/media/slide_1.174a9023.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}

.slider_1 div, .slider_2 div, .slider_3 div {
	padding-left: 15% !important
}
.slider_2 {
	background: url(/static/media/slide_2.2d90b321.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}
.slider_3 {
	background: url(/static/media/slide_3.f99f2605.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}

.slider_1 div h2, .slider_2 div h2, .slider_3 div h2 {
	font-family: 'SegoeUI' !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 2.5rem !important;
	margin-bottom: 20px
}

.slider_1 div .btn-saiba-mais, .slider_2 div .btn-saiba-mais, .slider_3 div .btn-saiba-mais, .btn-enviar {
	margin-top: 40px !important;
	/* width: 20%; */
	height: 50px !important;
	background: rgb(0,73,183);
	background: linear-gradient(90deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
	font-family: 'Roboto', sans-serif !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 1rem !important;
}

.homeCarrossel .awssld__bullets {
	bottom: 10px !important
}


.homeCarrossel .awssld__bullets button:not(.awssld__bullets--active) {
	background: #FFF !important;
}

.homeCarrossel .awssld__bullets--active {
	background: rgb(0,73,183) !important;
	background: linear-gradient(20deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
}

.parallax {
	background: url(/static/media/servicos.8dadb791.png) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}
.parallaxEmpresa {
	background: url(/static/media/background-empresa.ce1cafe3.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}
.parallaxContato {
	background: url(/static/media/background-contato.b682a425.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}
.parallaxPortas {
	background: url(/static/media/background-portas.de71f91a.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}

.btn-saiba-mais-portas {
	margin-top: 40px !important;
	width: 100%;
	height: 50px !important;
	background: rgb(0,73,183);
	background: linear-gradient(90deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
	font-family: 'Roboto', sans-serif !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 1rem !important;
}

.rs-nav-horizontal ul .rs-nav-item-active a {
	color: #1675e0 !important;
}

.rs-nav-horizontal ul li a {
	font-size: 1rem !important;
	font-family: 'SegoeUI' !important;
}

.rs-nav-horizontal ul li:hover a {
	color: rgba(0,73,183,1) !important;
	-webkit-text-decoration: dotted !important;
	        text-decoration: dotted !important;
}

.title-atendimento24::before {
	content: url(data:image/png;base64,);
	/* background-image: url(../assets/images/circle.png); */
	width: 45px;
	height:45px;
	position:relative;
	top: 5px;
	left: -15px;
	z-index: 999
}




input { height: 45px !important}

@media all and (min-width: 997px) {

	.title-atendimento24-deslocamento::after {
		content: '';
		color: #707070;
		position: absolute;
		left: 280px;
		top: 110px;
		width: 220px;
		height: 325px;
		display: inline-block;
		border-top: 5px dashed #bababa;
		z-index: 999;
	}
	.title-atendimento24-lupa::after {
		content: '';
		color: #707070;
		position: absolute;
		left: 330px;
		top: 110px;
		width: 170px;
		height: 325px;
		display: inline-block;
		border-top: 5px dashed #bababa;
		z-index: 999;
	}
	.title-atendimento24-deslocamento::after {
		content: '';
		color: #707070;
		position: absolute;
		left: 330px;
		top: 100px;
		width: 170px;
		height: 325px;
		display: inline-block;
		border-top: 5px dashed #bababa;
		z-index: 999;
	}

	.title-atendimento24-human::after {
		content: '';
		color: #707070;
		position: absolute;
		left: 330px;
		top: 110px;
		width: 180px;
		height: 325px;
		display: inline-block;
		border-top: 5px dashed #bababa;
		border-right: 5px dashed #bababa;
		border-bottom: 5px dashed #bababa;
		z-index: 999;
	}

	.card-human h3, .card-lupa h3 { width: 60%}
	.card-human h3 img, .card-lupa h3 img { float: right}

	.container-atendimento-24-orders div:nth-child(3){
		margin-top: 30px !important
	}
}
@media all and (max-width: 374px) {
	.slider_1 div, .slider_2 div, .slider_3 div {
		padding-left: 0px !important;
	}
}

@media all and (max-width: 767px) {
	.rs-footer div:nth-child(1), .rs-footer div:nth-child(2) { margin-left: 0px !important }
	.card-human, .card-lupa { margin-left: -30% !important}
	.btn-saiba-mais-nossas-portas { width: 50% !important}
	.nossas-portas-img { margin-bottom: 60px}
	.description-contact {margin-left: 0px !important}
	.container-atendimento-24-orders :nth-child(4)::after {
		content: '';
		color: #707070;
		position: absolute;
		left: 280px;
		top: 110px;
		width: 220px;
		height: 325px;
		display: inline-block;
		border-right: 5px dashed #bababa;
		z-index: 999;
	}



	.title-atendimento24-lupa::after {
		border-right: 5px dashed #bababa;
		/* border-bottom: 5px dashed #bababa; */
	}

	.container-atendimento-24-orders :nth-child(3)::after{
		border-right: 0px !important
	}

	.container-atendimento-24-orders :nth-child(3) {
		order: 4
	}





}

.rs-icon-whatsapp:hover {
	font-size: 4rem !important;
}
 .btn-wpp:hover {
	 height: 80px !important;
	 width: 80px !important
 }

 .btn-wpp {
	 text-decoration: none !important;
 }

